import {Pipe} from '@angular/core';
import timeago from 'timeago.js';

timeago.register('vi', (number, index, total_sec) => {
  // number: the timeago / timein number;
  // index: the index of array below;
  // total_sec: total seconds between date to be formatted and today's date;
  return [
    ['va xong', 'mt lc'],
    ['%s giy trc', '%s giy na'],
    ['1 pht trc', '1 pht na'],
    ['%s pht trc', '%s pht na'],
    ['1 gi trc', '1 gi na'],
    ['%s gi trc', '%s gi na'],
    ['1 ngy trc', '1 ngy na'],
    ['%s ngy trc', '%s ngy na'],
    ['1 tun trc', '1 tun na'],
    ['%s tun trc', '%s tun na'],
    ['1 thng trc', '1 thng na'],
    ['%s thng trc', '%s thng na'],
    ['1 nm trc', '1 nm na'],
    ['%s nm trc', '%s nm na']
  ][index];
});

@Pipe({
  name: 'timeAgo'
})
export class TimeAgoPipe {
  transform(date: any, locale?: string): string {
    locale = locale || 'vi';
    return timeago().format(date, locale);
  }
}
